import React from "react";
import {connect} from "react-redux";
import ProductListOfferPriceTag from "eshop/modules/simfree/components/filter/view/ProductListOfferPriceTag"
import VasPackages from "eshop/modules/configurator/components/lp/offers/VasPackages";
import PropositionDetailsForDevice from "eshop/modules/simfree/components/PropositionDetailsForDevice";
import PropositionFeatureForDevice from "eshop/modules/simfree/components/PropositionFeatureForDevice";
import {isProductDetailsPage} from "../selectors";

window.propositionCheckoutInputCount=0;
class PropositionForDeviceMobileView extends React.Component {
    constructor(props){
        super(props);
        let proposition=this.props.offer;
        window.propositionCheckoutInputCount++;
        this.state={ inputId : (this.props.inputName ? (this.props.inputName + "-") : "offerPlans-") +proposition.id + "-mobile"+window.propositionCheckoutInputCount};
    }
    isClientContext() {
        return this.props.clientContext || (this.props.contractRole ? true : false)
    }
    getOptionalDetails() {
        if (this.props.entryUnderChange) {
            let details;
            try {
                details = JSON.parse(this.props.entryUnderChange.details)
            } catch (err) {
                console.log("## details on cart parsing error##", err)
            }
            return {
                data: details
            }
        } else {
            return {}
        }
    }
    render() {
        let proposition=this.props.offer;


        return (
            <div className="l-row u-no-margin  ">
                <div className="o-radio opl-radio u-w-100 u-box-sizing u-padding-m">
                    <input id={this.state.inputId} key={this.state.inputId} type="radio" name={this.state.inputId}
                           checked={this.props.isSelected} className="switcher-trigger" />
                           <span className="o-ci"/>

                    <div>
                        <div className="l-row u-display_table u-block u-no-margin ">
                            <div
                                className="l-col l-col-12  u-clearfix u-display_table-cell  u-no-padding-r u-block u-no-padding-left">
                                <label className="o-ci-label u-font-normal g-black1-c u-no-padding" htmlFor={this.state.inputId}>
                                    <p>{this.props.offer.rateplanName}</p></label>
                            </div>
                        </div>
                    </div>
                </div>


                <div
                    className="l-col l-col-12  u-clearfix u-display_table-cell  u-no-padding-r u-block u-no-padding-left u-padding">
                    <p>Op≈Çata abonamentowa:</p>
                </div>
                <div
                    className="l-col l-col-12  u-clearfix u-display_table-cell  u-no-padding-r u-block u-no-padding-left">
                    <ProductListOfferPriceTag
                        proposition={proposition}
                        clientContext={this.props.clientContext}
                        contractRole={this.props.contractRole}
                        loyaltyLength={this.props.loyaltyLength}
                        alignClass={"u-left"}
                        mainPriceClass={""}
                        monthlyString="mies."
                        offerDiscountInfoDescription={this.props.offerDiscountInfoDescription}
                        offerDiscountInfoConvDescription={this.props.offerDiscountInfoConvDescription}
                        baseId="mobile"
                        key={"mobile_"+(!!this.props.clientContext?"clientContextOn":"clientContextOff")}
                    />
                </div>


                <div
                    className="l-col l-col-12  u-clearfix u-display_table-cell  u-no-padding-r u-block u-no-padding-left u-padding-top-m u-padding">
                    <p className="u-medium-hide u-inline-block">{(this.props.selectedOfferType == "DATA") ? "Pakiet danych na konto:" : "Internet w telefonie:"}</p>
                </div>
                <div
                    className="l-col l-col-12  u-clearfix u-display_table-cell  u-no-padding-r u-block u-no-padding-left ">
                    {proposition.features &&
                    <PropositionFeatureForDevice
                        proposition={proposition}
                    />}
                </div>

                <div
                    className="l-col l-col-12  u-clearfix u-display_table-cell  u-no-padding-r u-block u-no-padding-left u-padding-top-s u-padding">
                    <VasPackages offer={proposition}/>
                </div>
                {!!this.props.isProductDetailsPage&&
                <PropositionDetailsForDevice {...proposition} proposition={this.props.entryUnderChange || proposition}
                              clientContext={this.isClientContext()} {...this.getOptionalDetails()}/>}
            </div>

        )
    }
}

const mapStateToProps = state => ({
    isProductDetailsPage:isProductDetailsPage(state),
});

const mapDispatchToProps = dispatch => ({

});

export default connect(mapStateToProps, mapDispatchToProps)(PropositionForDeviceMobileView)

